.anxiousRoom {
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  height: 100vh;
  background: rgb(92,73,36);
  background: radial-gradient(circle, rgba(92,73,36,1) 0%, rgba(18,19,22,1) 100%);
}

#hex1 {
  right: -11em;
  top: 3em;
  opacity: 0;
}

#hex1 {
  position: relative;
  width: 20vh;
  height: 11.547619047619047vh;
  background-color: hsl(48.1,57.2%,44.9%);
  margin: 10vh 0;
}

#hex1:before,
#hex1:after {
  content: "";
  position: absolute;
  width: 0;
  border-left: 10vh solid transparent;
  border-right: 10vh solid transparent;
}

#hex1:before {
  bottom: 100%;
  border-bottom: 5.773182957393484vh solid hsl(48.1,57.2%,44.9%);
}

#hex1:after {
  top: 100%;
  width: 0;
  border-top: 5.773182957393484vh solid hsl(48.1,57.2%,44.9%);
}

